import { Component, OnInit } from '@angular/core';
// https://www.npmjs.com/package/plyr
import * as Plyr from 'plyr';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/app.reducer';
import { Video } from 'src/app/feature/shared/model/video';
import * as fromAction from '../../../shared/state/course.actions';
import { faEye } from '@fortawesome/free-solid-svg-icons';
import { CourseComplete } from 'src/app/feature/shared/model/courseComplete';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { faInstagramSquare, faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { SocialAuthService } from 'angularx-social-login';

declare var $: any;

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.scss']
})
export class CourseComponent implements OnInit {

  url = `${environment.host}/course`;

  faEye = faEye;
  faLinkedin = faLinkedin;
  faInstagram = faInstagramSquare;

  courseComplete: CourseComplete;

  pageLoaded = false;

  loggedIn: boolean = false;

  constructor(private readonly router: Router,
              private readonly state: Store<AppState>,
              private authService: SocialAuthService) {
    this.state.select('courseComplete').subscribe( courseComplete => {
      this.courseComplete = courseComplete;
    });

    if (this.courseComplete == null) {
      this.router.navigateByUrl('/not-found');
    }
  }

  ngOnInit(): void {
    const player = new Plyr('#player');
    //player.play();

    this.authService.authState.subscribe((user) => {
      this.loggedIn = (user != null);
      this.pageLoaded = true;
    });
  }

  changeVideo(newVideo: Video) {
    const action = new fromAction.ActualizarVideoActual(newVideo);
    this.state.dispatch(action);
  }

  expandCollanse(videos: Video[], id: string): boolean {
    const videosFilter = videos.filter( video => video.id = id);
    return videosFilter.length > 0;
  }

  ngAfterViewInit(){
    (function(d, s, id) {
      var js, fjs = d.getElementsByTagName(s)[0];
      js = d.createElement(s); js.id = id;
      js.src = "//connect.facebook.net/en_US/sdk.js#xfbml=1&version=v2.4";
  
      if (d.getElementById(id)){
        //if <script id="facebook-jssdk"> exists
        delete (<any>window).FB;
        fjs.parentNode.replaceChild(js, fjs);
      } else {
        fjs.parentNode.insertBefore(js, fjs);
      }
    }(document, 'script', 'facebook-jssdk'));
  }

  ejecutarCollapsed() {
    if(!this.loggedIn) {
      $('#loginModal').modal('show');
    }
  }

}
